import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
// import HospitalAdmission from '../QrCode/QrCode';

//import hospitalsData from './hospitalsData'

class Hospital extends Component {

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      showHospitalInfo:false
    };
  }

  componentDidMount() {

    fetch('http://iotapp.ephlux.com:3000/api/hospital/'+this.props.match.params.id, {
      headers: {
         'Authorization': 'Bearer '+localStorage.getItem('token'),
      }})
      .then(res => res.json())
      .then(
        (reponse) => {
          
          this.setState({
            isLoaded: true,
            items: reponse
          });
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )
  }
  showHospitalInfo=()=>{
    this.setState({showHospitalInfo:true})
  }
  render() {

    const hospital = this.state.items; //hospitalsData.find( hospital => hospital.id.toString() === this.props.match.params.id)

    let roleName = '-';

    if(hospital.roles !== undefined && hospital.roles.length !== 0){
      roleName = hospital.roles[0].name;
    }

    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={3}>
          </Col>
          <Col lg={6}>
            <Card>
              <CardHeader>
                <strong><i className="icon-info pr-1"></i>hospital Id: {this.props.match.params.id}</strong>
                {/* <button onClick={this.showHospitalInfo} className='btn btn-success  hospitalbtn'>Enter Admission Info</button> */}
              </CardHeader>
              <CardBody>
              
                  <Table responsive striped hover>
                  <tbody>                    
                    <tr>
                      <td>Name:</td>
                      <td><strong>{hospital.name}</strong></td>
                    </tr>
                    <tr>
                      <td>Address:</td>
                      <td><strong>{hospital.address}</strong></td>
                    </tr>
                    <tr>
                      <td>Latitude:</td>
                      <td><strong>{hospital.latitude}</strong></td>
                    </tr>
                   
                    <tr>
                      <td>Longitude:</td>
                      <td><strong>{hospital.longitude}</strong></td>
                    </tr>
                    <tr>
                      <td>Primery Phone</td>
                      <td><strong>{hospital.primary_phone}</strong></td>
                    </tr>
               
                    <tr>
                      <td>Secondary Phone:</td>
                      <td><strong>{hospital.secondary_phone}</strong></td>
                    </tr>
                    <tr>
                      <td>Email:</td>
                      <td><strong>{hospital.email}</strong></td>
                    </tr>
                    <tr>
                      <td>Fax:</td>
                      <td><strong>{hospital.fax}</strong></td>
                    </tr>
                    <tr>
                      <td>Status:</td>
                      <td><strong>{hospital.status==1?'Enable':'Disable'}</strong></td>
                    </tr>
                    <tr>
                      <td>Status Reason:</td>
                      <td><strong>{hospital.status_reason==null?'Reason is not define':hospital.status_reason}</strong></td>
                    </tr>
                
                  </tbody>
                </Table>
              
                 
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default Hospital;
